// 变量分析接口
import request from '@/interceptor.js';

/**
 *
 *
 * @export
 * @param {*} [data={}]
 * @returns promise
 */
export function queryDecision(data = {}) {
  return request({
    method: 'post',
    url: '/rc-analyse/decision/statistic',
    data,
    timeout: 60000
  });
}

/**
 *
 *
 * @export
 * @param {*} [data={}]
 * @returns promise
 */
export function exportDecision(data = {}) {
  return request({
    method: 'post',
    url: '/rc-analyse/decision/statisticExport',
    data,
    timeout: 60000
  });
}

/**
 *
 *
 * @export
 * @param {*} [data={}]
 * @returns
 */
export function dynamicDecision(data = {}) {
  return request({
    method: 'post',
    url: '/rc-analyse/decision/dynamicstatistic',
    data
  });
}

/**
 * 获取决策树
 *
 * @export
 * @param {*} callFrom 产品名称
 * @param {*} createTimeStart 开始时间
 * @param {*} createTimeEnd 结束时间
 * @param {number} moduleType 1 首贷、2 加贷 3 再贷
 * @returns promise
 */
export function getDecisionTree(callFrom, createTimeStart, createTimeEnd, moduleType) {
  if (moduleType !== undefined) {
    moduleType = Number(moduleType);
  }
  return request({
    method: 'post',
    url: '/rc-analyse/query/modeuleprocess/tree',
    data: {
      callFrom: callFrom,
      createTimeStart: createTimeStart,
      createTimeEnd: createTimeEnd,
      moduleType
    },
    timeout: 100000
  });
}
export function getDecisionTreeInterval(callFrom, createTimeStartInterval, createTimeEndInterval, moduleType) {
  if (moduleType !== undefined) {
    moduleType = Number(moduleType);
  }
  return request({
    method: 'post',
    url: '/rc-analyse/query/modeuleprocess/tree/timeinterval',
    data: {
      callFrom: callFrom,
      startTimeInterval: createTimeStartInterval,
      endTimeInterval: createTimeEndInterval,
      moduleType
    },
    timeout: 100000
  });
}

export function getRules(moduleName) {
  return request({
    method: 'post',
    url: '/rc-analyse/query/rcrule/getrules',
    data: {
      moduleName
    }
  });
}

export function getRuleDetail(ruleid) {
  return request({
    method: 'get',
    url: `/rc-analyse/query/rcrule/getruledetail/${ruleid}`,
    timeout: 100000
  });
}

export function getRuleCondCount(modulename, exprs, startDate, endDate) {
  return request({
    method: 'post',
    url: '/rc-analyse/Count/RuCondition',
    data: {
      modulename,
      exprs,
      startDate,
      endDate
    }
  });
}
