<template>
  <div class="ruleanalysis-view">
    <div class="return-page">
      <a href @click.prevent.stop="returnPrev">重新分析</a>
    </div>
    <el-card v-for="(one, i) in resultallList" :key="i">
      <div slot="header">
        <span>{{ getTitle(i) }} 数据统计图</span>
      </div>
      <div class="wrap-result-table">
        <div :id="`resultbar${i}`" class="resultbar" v-if="one.resultall.length && one.showbar"></div>
        <span class="nodata el-table__empty-block" v-if="!one.resultall.length || !one.showbar">暂无数据</span>
      </div>
    </el-card>
    <el-card v-for="(one, i) in resultallList" :key="i + resultallList.length">
      <div slot="header">
        <span>{{ getTitle(i) }} 数据统计结果</span>
      </div>
      <el-table :data="one.resultall">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="key" label="变量取值" align="center"></el-table-column>
        <el-table-column prop="address" :label="`${getTitle(i)} 结果值 `" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.value===null ? "null" : scope.row.value}} {{showPercent(i,scope.row.value)}}</span>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import mixinsVar from '@/mixins/mixins-var.js';
import mixinsRule from '@/mixins/mixins-rule.js';
import {getVarList} from '@/api/sample.js';
import {saveAnalysis, queryAnalysisDetail} from '@/api/analysis.js';
import {queryDecision, exportDecision} from '@/api/decision.js';
import {getOnePrefilter, getSectionContent, getOneVarSelect, getOverdueList} from '@/assets/scripts/getSomeSetting.js';

export default {
  name: 'ruleanalysis-view',
  mixins: [mixinsVar, mixinsRule],
  data() {
    return {
      resultallList: [
        {
          showbar: false,
          postdata: {},
          resultall: []
        }
      ],
      decisionDates: [],
      loanDates: [],
      indicator: [],
      varList: [],
      overdue: [],
      prefilter: [getOnePrefilter()],
      varselectlist: [getOneVarSelect()],
      sectioncontent: getSectionContent(), // 变量分组方式可选的内容
      overduelist: getOverdueList(),
      showfilesave: false,
      filesaved: false
    };
  },
  computed: {},
  created() {
    this.recordId = this.$route.params.recordid;
    this.getAnalysisResult()
      .then(result => {
        if (Array.isArray(result)) {
          this.resultallList = result;
          this.postdata = this.initFields(result);
        }
      })
      .catch(err => console.log(err));
  },
  methods: {
    returnPrev() {
      this.$store.commit('UPDATE_VAR_TYPE', 'ruleanalysis');
      if (this.filesaveid) {
        this.$store.commit('UPDATE_ANALYSISRESULT', {
          name: this.filename,
          id: this.filesaveid
        });
        this.$router.push({
          path: '/variate',
          query: {
            recordid: this.filesaveid
          }
        });
      } else {
        this.$router.push('/variate');
      }
    },
    getAnalysisResult() {
      return new Promise((resolve, reject) => {
        if (this.recordId) {
          // 获取保存的分析结果
          queryAnalysisDetail(this.recordId)
            .then(result => {
              if (result.result == null) {
                this.$message.warning('此历史记录不存在');
                reject('此历史记录不存在');
                return;
              }
              this.filename = result.result.name;
              this.filesaveid = this.recordId;
              this.filesaved = true;
              const resultallList = JSON.parse(result.result.result);
              this.$store.commit('UPDATE_ANALYSISRESULT', {
                key: 'ruleanalysis',
                value: resultallList
              });
              resolve(resultallList);
            })
            .catch(err => reject(err));
        } else {
          const resultallList = this.$store.state.analysisResult.ruleanalysis;
          const file = this.$store.state.analysisResult.file;
          this.filename = file.name;
          this.filesaveid = file.id;
          resolve(resultallList);
        }
      });
    },
    getVars(val, isMain = true, target, isInit = false) {
      target = isMain ? this : target;
      target.rcProductName = val[0];
      target.moduleType = val[1];
      target.samplename = val[2];
      target.initSample = val;
      getVarList(target.samplename)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (!result.result.length) {
              return;
            }
            target.varList = result.result.map(({key, vbname, fieldtype, transmittype}) => {
              return {
                label: `${vbname}${fieldtype === 'float' ? '（连续）' : '（离散）'}`,
                value: key,
                fieldtype,
                transmittype
              };
            });
            if (isMain && isInit) {
              this.initFieldsAboutVar(this.postdata);
              this.$nextTick(() => {
                for (let i = 0; i < this.resultallList.length; i++) {
                  this.drawBar(this.resultallList[i].resultall, false, i);
                }
              });
            }
          }
        })
        .catch(err => console.log(err));
    },
    showPercent(i, value) {
      if (value == null) {
        return '（0%）';
      }
      let data = this.resultallList[i].postdata;
      if (this.resultallList[i].ispercent) {
        //贷前显示百分比
        let allCount = 0;
        for (let item of this.resultallList[i].resultall) {
          let v = item.value ? parseFloat(item.value) : 0;
          allCount += v;
        }
        let rate = allCount > 0 ? ((value / allCount) * 100).toFixed(2) : 0;
        return '（' + rate + '%）';
      } else if (data.resultNeedPercent) {
        return '（%）';
      } else {
        return '';
      }
    },
    checkSource() {
      if (this.samplename.length === 0) {
        this.$message.warning('请选择样本');
        return;
      }
      if (this.indicator.length === 0) {
        this.$message.warning('请选择指标类型');
        return;
      }
      if (this.indicator.indexOf('AFTER_LOAN') > -1 && this.overdue.length === 0) {
        this.$message.warning('请选择逾期指标');
        return;
      }
      return true;
    }
  }
};
</script>

<style scoped>
.ruleanalysis-view {
  padding-bottom: 60px;
}
.el-card {
  margin: 20px 15px;
}
.resultbar {
  width: 100%;
  height: 300px;
  margin: 0 auto;
}
.nodata {
  color: #999;
}
</style>
